<template>
  <div  >
<!--table样例-->
    <el-dropdown
      :hide-on-click="false"
      style="float:right;margin-right: 20px;"
    >
      <el-button type="primary" size="mini" title="显示隐藏列" > <i class="el-icon-s-grid "></i><i class=" el-icon-arrow-down el-icon--right"></i></el-button>

      <el-dropdown-menu slot="dropdown">
        <el-scrollbar  style="height: 40vh; width:100px;">
            <el-checkbox-group v-model="colsChecked">
            <el-dropdown-item v-for="item in colsMap" :key="item.value">
              <el-checkbox @change="colCheckCommand" :label="item.value" :key="item.value">{{item.caption}}</el-checkbox>
            </el-dropdown-item>
          </el-checkbox-group>
        </el-scrollbar>

      </el-dropdown-menu>
    </el-dropdown>


<!--    :style="{'width': '900px'}"-->
<!--    style="width: 100%;font-size: 14px;"-->
    <el-table
      border

      ref="multipleTable"
      :data="tableData"
      tooltip-effect="dark"
      @sort-change="sortColumn"

      :row-style="{'height':'10px'}"
      :cell-style="{'padding':'5px 0px'}"
      @selection-change="handleSelectionChange"
      @row-click="handleSelectCurrent"
      :header-cell-style="{'background': '#4B86BC','color':'#ffffff','borderRight': '1px solid #E3E5EB' }"
    >
<!--      :span-method="spanRowAndCol"-->

      <el-table-column
        type="selection"
        width="55">
      </el-table-column>

<!--      <el-table-column-->
<!--        type="index"-->
<!--        label="序号"-->
<!--        :index="indexMethod"-->
<!--       >-->
<!--      </el-table-column>-->
      <el-table-column
        sortable
        _fixed
        prop="date"
        label="日期"
        v-if="colsMap[0].isShow"
        width="120">
<!--        <template slot-scope="scope">{{ scope.row.date }}</template>-->
      </el-table-column>

      <el-table-column
        prop="name"
        label="姓名"
        v-if="colsMap[1].isShow"
        sortable
        width="120">
      </el-table-column>
      <el-table-column
        prop="address"
        label="地址"
        v-if="colsMap[2].isShow"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="province"
        label="城市"
        v-if="colsMap[3].isShow"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="city"
        label="区域"
        v-if="colsMap[4].isShow"
        show-overflow-tooltip>

      </el-table-column>
      <el-table-column
        _fixed="right"
        prop="position"
        label="邮编"
        v-if="colsMap[5].isShow"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="age"
        label="年龄"
        v-if="colsMap[6].isShow"
        align="center"
        show-overflow-tooltip

      >

        <template slot-scope="scope">

          <el-tag type="warning" effect="dark" size="mini" v-if="scope.row.age<30">{{formatterAge(scope.row.age)}}</el-tag>
          <el-tag type="success" effect="dark" size="mini" v-else-if="scope.row.age>80">{{formatterAge(scope.row.age)}}</el-tag>
          <el-tag type="danger"  effect="dark" size="mini" v-else>{{formatterAge(scope.row.age)}}</el-tag>
        </template>

      </el-table-column>

      <el-table-column
        label="操作"
        width="150"
      >
        <template slot-scope="scope">
          <el-button
            type="primary" icon="el-icon-edit" circle
            size="mini"
            @click.native.stop="handleEditRow(scope.$index, scope.row)"></el-button>
          <el-button
            type="danger"  icon="el-icon-delete" circle
            size="mini"
            @click.native.stop="handleDeleteRow(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>


    </el-table>
    <el-pagination
      background
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[10, 20, 50, 100,200]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="totalRow">
    </el-pagination>

<!--    <el-button type="primary" @click="openMsgBox">确定</el-button>-->


  </div>

</template>

<script>
export default {
  name: "Example1",
  data() {
    return {
      currentPage:1,
      pageSize:10,
      totalRow:0,

      colsChecked:[
        'date','name','address','province','city','position','age'
      ],
      colsMap:[
        {caption:'日期',value:'date',isShow:true},
        {caption:'姓名',value:'name',isShow:true},
        {caption:'地址',value:'address',isShow:true},
        {caption:'城市',value:'province',isShow:true},
        {caption:'区域',value:'city',isShow:true},
        {caption:'邮编',value:'position',isShow:true},
        {caption:'年龄',value:'age',isShow:true},
      ],
      tableData: [{
        date: '2016-05-03',
        name: '王小虎1',
        address: '上海市普陀区金沙江路 1518 弄',
        province: '上海',
        city: '普陀区1',
        position: 200333,
        age:"20"
      }, {
        date: '2016-05-02',
        name: '王小虎1',
        address: '上海市普陀区金沙江路 3518 弄',
        province: '上海',
        city: '普陀区2',
        position: 200331,
        age:"30"
      }, {
        date: '2016-05-04',
        name: '王小虎1',
        address: '上海市普陀区金沙江路 2518 弄',
        province: '上海',
        city: '普陀区3',
        position: 200334,
        age:"10"
      }, {
        date: '2016-05-01',
        name: '王小虎4',
        address: '上海市普陀区金沙江路 4518 弄',
        province: '北京',
        city: '朝阳区',
        position: 100100,
        age:"40"
      }, {
        date: '2016-05-08',
        name: '王小虎5',
        address: '上海市普陀区金沙江路 5518 弄',
        province: '北京',
        city: '朝阳区2',
        position: 1001002,
        age:"120"
      }, {
        date: '2016-05-06',
        name: '王小虎6',
        address: '上海市普陀区金沙江路 7518 弄',
        province: '天津',
        city: '南岗区',
        position: 200100,
        age:"35"
      }, {
        date: '2016-05-07',
        name: '王小虎6',
        address: '上海市普陀区金沙江路 6518 弄',
        province: '天津',
        city: '开发区',
        position: 203100,
        age:"80"
      }],
      multipleSelection: [],

      sortOrderCol:'',

      spanColArr: [],
      spanRowArr: [],
      pos: 0,

    }
  },
  mounted:function(){
    console.info("Example1.vue mounted get route params :"+ JSON.stringify(this.$route.params));
    console.info("Example1.vue mounted get route query :"+ JSON.stringify(this.$route.query));
    this.getSpanArr(this.tableData);
    this.tableData.map((v, i, s) => {
      if (v.age === "40") {
        this.spanColArr.push(i);
      }
    });

  },
  methods:{
    openMsgBox() {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).then(() => {
        this.$message({
          type: 'success',
          message: '删除成功!'
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    // toggleSelection(rows) {
    //   if (rows) {
    //     rows.forEach(row => {
    //       this.$refs.multipleTable.toggleRowSelection(row);
    //     });
    //   } else {
    //     this.$refs.multipleTable.clearSelection();
    //   }
    // },
    handleSelectCurrent(row){
       this.$refs.multipleTable.toggleRowSelection(row);

    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
      console.info("选择的行："+JSON.stringify( this.multipleSelection) );
    },
    indexMethod(index) {
      return index ++;
    },
    formatterAge(  cellValue ) {
      // console.info("row:"+JSON.stringify(row));
        return  cellValue+"岁";
    },
    getSpanArr(data) {
      this.spanRowArr = [];
      if(data==null){
        return;
      }
      for (var i = 0; i < data.length; i++) {
        if (i === 0) {
          this.spanRowArr.push(1);
          this.pos = 0;
        } else {
          // 判断当前元素与上一个元素是否相同（name为标记）
          if (data[i].name === data[i - 1].name) {
            this.spanRowArr[this.pos] += 1;
            this.spanRowArr.push(0);
          } else {
            this.spanRowArr.push(1);
            this.pos = i;
          }
        }
      }
    },
    spanRowAndCol({ row, column, rowIndex, columnIndex }){

      //针对某行处理列的合并
      if (this.spanColArr.includes(rowIndex)) {
        if (columnIndex === 0) {//返回合并1条记录，合并4个列
          return {
            rowspan: 1,
            colspan: 4
          };
        }
        else if (columnIndex === 1 || columnIndex === 2 || columnIndex == 3) {//返回值是空白
          return {
            rowspan: 0,
            colspan: 0
          };
        }
      }
      //针对某列处理行的合并，多行相同的值进行合并列
      // if (columnIndex === 0 || columnIndex === 2 || columnIndex === 3) {
      if (columnIndex === 1) {
        const _row = this.spanRowArr[rowIndex];
        const _col = _row > 0 ? 1 : 0;
        return {
          rowspan: _row,
          colspan: _col
        };
      }

    },
    handleEditRow(index, row) {
      console.log("行号："+index+",行内容：" + JSON.stringify(row) );
    },
    handleDeleteRow(index, row) {
      console.log("行号："+index+",行内容：" + JSON.stringify(row) );
      this.$confirm('请再次确认是否要删除[<span style="color:red;">'+row.name+'</span>]该行记录?', '删除提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        dangerouslyUseHTMLString:true,
        closeOnClickModal:false,
        closeOnPressEscape:false,

      }).then(() => {
        this.$message({
          type: 'success',
          message: '删除成功!'
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    colCheckCommand(){
      console.info("colsChecked："+this.colsChecked);
      this.colsMap.filter(i => {
        console.info("遍历"+i.value);
        if (this.colsChecked.indexOf(i.value) !== -1) {
          console.info("选中集合中存在"+i.value);
          i.isShow = true
        } else {
          i.isShow = false
        }
      })

     console.info('colsList:'+JSON.stringify(this.colsMap));
    },
//自定义排序      column参数为一个对象包含需要排序的属性和排序方法
    sortColumn(column) {
      console.log("已经排序项：" + JSON.stringify(column));
      let orderFlag = '';
      if (column.order != null) {
        if (column.order === "descending") {
          orderFlag = 'desc';
        } else if (column.order === "ascending") {
          orderFlag = 'asc';
        }
        console.info("属性名称："+ column.prop+",排序方式："+column.order);

           this.sortOrderCol = column.prop + " " + orderFlag;
        console.info("用户选择排序列："+this.sortOrderCol);
      }

    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    }
  }

}
</script>

<style scoped>


</style>
<style>
.el-scrollbar__wrap {
  overflow-x: hidden !important;
  /*overflow-y: hidden !important;*/
}
</style>



